export const required = a => !!a || '必填欄位'
export const email = a => /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/.test(a) || '輸入的Email格式有誤'

export const emailRule = (value) => {
    const pattern = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    return pattern.test(value) || '請輸入有效的電子郵件地址';
};
export const pwRule = (value) => {
    const pattern = /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[\W_]).{12,30}$/;
    return pattern.test(value) || '密碼需包含小寫英文、大寫英文、數字及符號，且長度為12~30個字';
};
export const pwConfirmRule = (pw, confirmPw) => {
    return pw === confirmPw || '密碼確認輸入不一致';
};
export const phoneRule = (value) => {
    const pattern = /^09\d{8}$/;
    return pattern.test(value) || '請輸入有效的手機號碼';
};
export const nameRule = (value) => {
    return value && value.trim().length > 0 || '姓名不可為空';
};
export const unitRule = (value) => {
    return value && value.trim().length > 0 || '單位不可為空';
};
export const codeRule = (value) => {
    return value && value.trim().length == 6 || '驗證碼需為6位數字';
};
export const userNumberRule = (value) => {
    // const pattern = /^\d+$/;
    // return pattern.test(value) || '使用者編號僅能包含數字';
    return true;
};